import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-navbar',
  templateUrl: './navbar.component.html',
  styleUrls: ['./navbar.component.css'],
  inputs: ['btnText']
})
export class NavbarComponent implements OnInit {
  btnText: string;
  private width: number = window.outerWidth;
  bigScreen: boolean = this.width > 991;
  smallScreen: boolean = this.width <= 991;

  secondStatus: boolean = false;
  firstStatus: boolean = false;
  thirdStatus: boolean = false;
  
  firstLink(){
    this.firstStatus = !this.firstStatus;
    this.secondStatus = false;
    this.thirdStatus = false;       
  }

  secondLink(){
    this.secondStatus = !this.secondStatus; 
    this.firstStatus = false;
    this.thirdStatus = false;     
  }

  thirdLink(){
    this.thirdStatus = !this.thirdStatus;  
    this.firstStatus = false;
    this.secondStatus = false;     
  }

  constructor() { }

  ngOnInit() {
  }

  public isCollapsed = true;
}
